<template>
    <div class="grid grid-cols-9 pt-10 text-gray-400 m-10 h-80">
        <div class="col-span-2"></div>
        <div :class="getShadowClass()"
            class="col-span-5 grid grid-cols-2 bg-stone-950 shadow-lg overflow-hidden rounded-xl">
            <div class="black-shadow p-4 z-40">
                <div class="grid grid-cols-4">
                    <div class="col-span-1">
                        <img :src="movie.poster" alt="poster" class="w-20 h-28">
                    </div>
                    <div class="col-span-3">
                        <h1 class="text-xl">{{ movie.title }}</h1>
                        <p class="text-sm text-gray-500"> {{ movie.year }},producer</p>
                        <p class="mt-5">120min {{ movie.genres }}</p>
                    </div>
                </div>
                <div class="h-1/6 text-xs pt-3">
                    <p>there is no any text for this </p>
                </div>
                <div class="space-x-6 pt-3">
                    <button> <i class="fas fa-share-alt"></i></button>
                    <button> <i class="fas fa-heart"></i></button>
                    <button> <i class="fas fa-comment-alt"></i></button>
                </div>
            </div>
            <div>
                <img :src="movie.poster" alt="" class="ms-auto">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        movie: Object,
        index: Number // Fix typo in object type
    },
    data() {
        return {};
    },
    methods: {
        getShadowClass() {
            if (this.index % 3 === 0) {
                return 'red-shadow';
            }
            if (this.index % 3 === 1) {
                return 'green-shadow';
            }
            if (this.index % 3 === 2) {
                return 'yellow-shadow';
            }
        }
    },
};
</script>

<style>
.black-shadow {
    box-shadow: 0px 0px 50px 100px #0c0a09;
}
.yellow-shadow {
    box-shadow: 0px 0px 50px 1px rgba(191, 194, 22, 0.373);
}

.red-shadow {
    box-shadow: 0px 0px 50px 1px rgba(194, 28, 22, 0.373);
}

.green-shadow {
    box-shadow: 0px 0px 50px 1px rgba(22, 194, 94, 0.373);
}
</style>