<template>
  <div>
    <movieInformation v-for="(movie, index) in movies" :key="index" :index="index" :movie="movie"></movieInformation>
    <div class="text-center m-10">
      <button class="text-white hover:text-white font-bold py-2 px-4 rounded" @click="showMore">
        <span> show more</span><br>
        <p class="rotate-90">></p>
      </button>
    </div> 
  </div>
</template>

<script>
import axios from 'axios';
import movieInformation from './components/movieInformation.vue';
export default {
  name: 'App',
  components: {
    movieInformation,
  },
  data() {
    return {
      movies: [],
      page: 1,
      loading: false,
    };
  },
  methods: {
    async getMovies() {
      try {
        this.loading = true;
        const response = await axios.get(`https://moviesapi.ir/api/v1/movies?page=${this.page}`);
        this.movies = [...this.movies, ...response.data.data.map(movie => ({
          title: movie.title,
          poster: movie.poster,
          year: movie.year,
          country: movie.country,
          rate: movie.imdb_rating,
          genres: movie.genres.toString(),
        }))];
        console.log(this.movies[1].genres);
        this.loading = false;
      } catch (error) {
        console.error(error);
        this.loading = false;
      }
    },
    showMore() {
      this.page++;
      this.getMovies();
    },
  },
  created() {
    this.getMovies();
  },

}
</script>
